import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SignupComponent } from './pages/signup/signup.component';
import { SigninComponent } from './pages/signin/signin.component';
import { HomeComponent } from './pages/home/home.component';
import { MatchCreateComponent } from './pages/match-create/match-create.component';
import { FindPlayerComponent } from './pages/find-player/find-player.component';
import { FindMatchComponent } from './pages/find-match/find-match.component';
import { StartComponent } from './pages/start/start.component';
import { LevelComponent } from './pages/level/level.component';
import { FindLocalComponent } from './pages/find-local/find-local.component';
import { MatchCreatedComponent } from './pages/match-created/match-created.component';


const routes: Routes = [
    { path: 'start', component: StartComponent },
    { path: 'signin', component: SigninComponent },
    { path: 'signup', component: SignupComponent },
    { path: 'level', component: LevelComponent },
    { path: 'home', component: HomeComponent },
    { path: 'match-create', component: MatchCreateComponent },
    { path: 'find-player', component: FindPlayerComponent },
    { path: 'find-match', component: FindMatchComponent },
    { path: 'find-local', component: FindLocalComponent },
    { path: 'match-created', component: MatchCreatedComponent },

    // otherwise redirect to home
    { path: '**', redirectTo: 'start' }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
